// Imports
@import url('https://fonts.googleapis.com/css2?family=Francois+One&display=swap');

// Variable
$background: #121322;
$control-bg: #1e1945;
$control-outline: white;


html {
  background: $background;
}

body {
  margin: 0;
}

.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  width: 100vw;
  height: 100vh;
}

.control {
  width: 80%;
  text-align: center;
  margin: 10px;
  background: $control-bg;
  color: white;
  border: solid white 3px;
  font-family: 'Francois One', sans-serif;
  font-size: 400%;

  cursor: pointer;

  &:hover {
    background: $control-outline;
    color: $control-bg;
    border-color: $control-bg;
  }
}
